
#app {
  font-family: '\5FAE\8F6F\96C5\9ED1', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
ul {
  list-style-type:none;
}
#tabs>ul {
  white-space:nowrap;
}
#tabs>ul li {
  display:inline-block;
  float: none;
  width: 50%;
  margin: 0 -0.1em;
}
#tabs>ul li a {
  width: 100%;
  text-align: center;
}
.news ul {
  margin: 0 -3.5em;
}
.ui-widget-content a {
  color: #003eff
}
.ui-widget {
  font-family: '\5FAE\8F6F\96C5\9ED1', Helvetica, Arial, sans-serif;
}
